<template>
 <div id="app">
    <div class="result">读的单词：{{result}}</div>
    <div class="voice-input-button-wrapper">
      <voice-input-button
        appId="5e66f754"
        apiKey="ae6f52a8c0240d2883aae9b28ff7b6c8"
        apiSecret="5e69f593cb8891d9e9b8384a1d0c46d9"
        pd="game"
        returnMode="complete"
        language="en_us"
        v-model="result"
        @record="showResult"
        @record-start="recordStart"
        @record-stop="recordStop"
        @record-blank="recordNoResult"
        @record-failed="recordFailed"
        @record-ready="recordReady"
        @record-complete="recordComplete"
        interactiveMode="press"
        color="#fff"
        domain = '1'
        tipPosition="top"
      >
        <template slot="recording">大声说出你看到的东西</template>
      </voice-input-button>
    </div>
  </div>
</template>

<script>
  import voiceInputButton from 'voice-input-button2'
  export default {
    data () {
      return {
          result: ''
      }
    },
    components:{voiceInputButton},
    methods:{
      recordReady () {
            console.info('按钮就绪!')
          },
          recordStart () {
            console.info('录音开始')
          },
          showResult (text) {
            console.info('收到识别结果：', text)
          },
          recordStop () {
            console.info('录音结束')
          },
          recordNoResult () {
            console.info('没有录到什么，请重试')
          },
          recordComplete (text) {
            console.info('识别完成! 最终结果：', text)
          },
          recordFailed (error) {
            console.info('识别失败，错误栈：', error)
          }
    }
  }
</script>


<style>
  html, body {
    margin: 0;
    width: 100%;
    height: 100%;
  }
  *{
    box-sizing: border-box;
  }
  #app {
    position: absolute;
    top: 100px;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .voice-input-button-wrapper{
    width: 42px;
    height: 42px;
    background-color: mediumpurple;
    border-radius: 50%;
  }
  .result{
    width: 70%;
    padding: 25px;
    border: #e2e2e2 1px solid;
    border-radius: 5px;
    font-size: 16px;
    color: #727272;
    min-height: 24px;
    margin-bottom: 25px;
  }
</style>



